import React, { useState, useRef, useEffect } from "react";
import ReactDOM from "react-dom";
import { IParallax, Parallax, ParallaxLayer } from '@react-spring/parallax'
import Projects, { ProjectsR } from "./Projects";
import { useScroll } from "@react-spring/web";
import { animate, motion } from "framer-motion";

var colorIndex = 0;

function button_animator() {
    for (let di = 0; di < document.querySelectorAll(".button").length; di++) {
        let index = di; // Capture the current value of di in a separate variable
        document.querySelectorAll(".button")[index].addEventListener("click", function () {
            document.querySelectorAll(".button")[index].classList.add("clicked");
            setTimeout(function () {
                document.querySelectorAll(".button")[index].classList.remove("clicked");
            }, 500);
        });
    }
}
button_animator();
function changeColor() {
    var list_bg_color = ["#364F6B", "#3498db"];
    var list_bg2_color = ["#FC5185", "#e74c3c"];
    var list_bg3_color = ["#3FC1C9", "#2ecc71"];
    var list_text_color = ["#F5F5F5", "#ffffff"];

    const root = document.documentElement;

    colorIndex = (colorIndex + 1) % list_bg_color.length;

    root.style.setProperty('--color_pg1', list_bg_color[colorIndex]);
    root.style.setProperty('--color_pg2', list_bg2_color[colorIndex]);
    root.style.setProperty('--color_pg3', list_bg3_color[colorIndex]);
    root.style.setProperty('--text_color', list_text_color[colorIndex]);
}

function Parallaxs() {

    const parallaxRef = useRef<IParallax>(null);
    const [ScrollTop, setScrollTop] = useState<number>(0);

    useEffect(() => {
        if (!parallaxRef.current || !parallaxRef.current.container) return
        parallaxRef.current.container.current.onscroll = () => {
            setScrollTop(parallaxRef.current?.current as number)
        }
    })




    var percentScroll = ((ScrollTop / (window.innerHeight * 2)) * 100);
    let text = "PROGRAMMER";
    if (percentScroll > 0 && percentScroll <= 37) {
        text = "PROGRAMMER";
    } else if (percentScroll > 37 * 0.7 && percentScroll <= 85) {
        text = "PROJECTS";
    } else if (percentScroll > 85 && percentScroll <= 100) {
        text = "ABOUT";
    }

    var percentScrollRelX = (percentScroll * window.innerWidth) / 100;

    return (
        <Parallax ref={parallaxRef} pages={3} style={{ top: '0', left: '0' }} className="animation"  >
            <div className="page-1" >
                <ParallaxLayer offset={0} speed={2.5} style={{ zIndex: 20 }}>
                    <button className="button proj text_color" onClick={changeColor} >Theme change -</button>
                    <button className="button github text_color" onClick={() => window.open("https://github.com/arnavgupta00", "blank")}>Github -</button>
                    <div className="bar">
                        <img onClick={() => window.open("https://www.instagram.com/arnav_gupta3035/", "blank")} className="dlogo" src="./Logo/dlogo.png" alt="D Logo"></img>
                        <img onClick={() => window.open("https://www.instagram.com/arnav_gupta3035/", "blank")} className="ilogo" src="./Logo/insta_logo.png" alt="Instagram Logo"></img>
                    </div>
                    <img className="bar-tag" src="./Logo/LOGO.png" alt="Arnav Gupta Logo"></img>
                </ParallaxLayer>
                <ParallaxLayer offset={0} speed={3}>
                    <div className="text-line-1">
                        <div className="text-1-div">
                            <h1 className="text-1 text_color">I'm </h1>
                        </div>
                        <div className="text-2-div">
                            <h2 className="text-2 text_color">Arnav</h2>
                        </div>

                    </div>
                </ParallaxLayer>
                <ParallaxLayer>
                    <motion.div className="rollX" animate={{ x: -percentScrollRelX * 3 }}><svg width={window.innerWidth} height={window.innerHeight / 30} xmlns="http://www.w3.org/2000/svg">
                        <rect x="10" y="10" width={window.innerWidth} height={window.innerHeight / 50} stroke="#F5F5F5" stroke-width="2" fill="#F5F5F5" />
                    </svg></motion.div>
                </ParallaxLayer>
                <ParallaxLayer offset={1} speed={-1}>
                    <motion.h1 animate={{ x: text == "PROJECTS" ? (0.6 * window.innerWidth) : 0 }} className="text-3 text_color">{text  }.</motion.h1>
                </ParallaxLayer>
                <ParallaxLayer offset={0} speed={2.5}>
                    <div className="about-me text_color">
                        <h3>About me</h3>
                        <p>I was born in Punjab , India . I have a keen interest in software development , currently i am pursuing computer engineering in Thapar University , Patiala, Punjab.
                        </p>
                    </div>
                </ParallaxLayer>
            </div>
            <motion.div className="page-2" >
                <ParallaxLayer offset={1} speed={2.5}>
                    <Projects style={{ zIndex: 20 }} img="./images/blog.png" title="Blog Website" techUsed="This is a blog website which stores its data in MongoDB format and backend is done in Node" link="https://github.com/arnavgupta00/Blog-Website-Mongo"></Projects>
                    <ProjectsR style={{ zIndex: 20 }} img="./images/todo.png" title="TODO App" techUsed="It is a TODO app which has ample use special effects and it stores its data in MongoDB format and backend is done in Node" link="https://github.com/arnavgupta00/TODO-APP-MONGO"></ProjectsR>
                    <Projects style={{ zIndex: 20 }} className="downImg" img="./images/bomber.png" title="SMS Bomber" techUsed="This is an SMS bomber created by Arnav" link="https://github.com/arnavgupta00/Sms_bomber"></Projects>
                    <ProjectsR style={{ zIndex: 20 }} img="./images/cryptoTracker.png" title="CryptoTracker" techUsed="It is an app which outputs track price and volume of CryptoCurrency  and it takes its data from a free REST API based API " link="https://github.com/arnavgupta00/CryptoTracker"></ProjectsR>
                    <Projects style={{ zIndex: 20 }} img="./images/hungrySnake.png" title="Hungry Snake" techUsed="This is a very simple game made by using Pygame. Feel free to download the source code :)" link="https://github.com/arnavgupta00/Hungry_Snake"></Projects>
                </ParallaxLayer>
            </motion.div>
            <div className="page-3" >
                <ParallaxLayer offset={2} speed={3}>
                    <motion.div animate={{y: percentScroll >95 ? -50 : 50}} className="education">
                        <h1>Education: </h1>
                        <p>Pursuing Computer Science and Business Systems from Thapar University </p>
                    </motion.div>
                </ParallaxLayer>
                <ParallaxLayer offset={2} speed={2}>
                    <motion.div animate={{y: percentScroll >95 ? -50 : 50}} className="techExcel"><h1>Technologies I Excel At: </h1>
                        <p>Web Dev: Node, Express.js , MongoDB , React.js , React spring.</p>
                        <p>App Dev: Kivy, Pygames .</p>
                    </motion.div>
                </ParallaxLayer>
                <ParallaxLayer offset={2} speed={2.5}>
                    <motion.div animate={{y: percentScroll >95 ? -50 : 50}} className="techKnow" >
                        <h1>Languages I Can Work With : </h1>
                        <p>C/C++ , Html , CSS , JavaScript , Python </p>
                    </motion.div>
                </ParallaxLayer>
                <ParallaxLayer offset={2} speed={2}>
                    <footer>contact: arnavguptagg@gmail.com</footer>
                </ParallaxLayer>
            </div>
        </Parallax>
    );
}

export default Parallaxs;


