import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import {
    motion,
    useScroll,
    useSpring,
    useTransform,
    useMotionValue,
    useVelocity,
    useAnimationFrame
} from "framer-motion";


function Projects(props) {

    return <div className="projects">
        <div >

            <motion.img drag whileHover={{ x:200 , scale: 2 }} style={{zIndex:25}} src={props.img}></motion.img>
        </div>


        <div style={{ zIndex: 10 }} className="projects_text">
            <a className="anchor" style={{textDecoration: "None"}} target="_blank" href={props.link}><h3>{props.title}</h3></a>
            <p>{props.techUsed}</p>
        </div>

    </div>
}

function ProjectsR(props) {

    return <div className="projectsR">
        <div >

        <motion.img drag transform= {{x:0,y:0}} whileHover={{ x:-200 , scale: 2 }} style={{zIndex:25}} src={props.img}></motion.img>
        </div>


        <div style={{ zIndex: 10 }} className="projects_textR">
            <h3><a className="anchor" target="_blank" href={props.link}>{props.title}</a></h3>
            <p>{props.techUsed}</p>
        </div>

    </div>
}

export default Projects;
export { ProjectsR };